.projectLayoutContainer{
    display: flex;
    flex-direction: column;
}

.hero {
    display: flex;
    flex-direction: column;
    /* background-image: url('defaultEvent.jpeg'); */
    background-repeat: no-repeat;
    background-size: 1400px 1000px;
    /* opacity : 0.5; */
    /* filter: brightness(50%); */
    
}

.heroHeader {
    display: flex;
    filter: brightness(200%);
}

.projectName {
    margin-top: 10vh;
    margin-left: 10vw;
    font-size: 2rem;
    flex: 50%;
    color: darkslategrey;
}

.offeredBy {
    /* margin-top: 10vh; */
    /* margin-left: 10vw; */
    font-size: 2.5rem;
    flex: 50%;
    color: #0077b6;
    font-weight: bold;
}

.projectDiscription {
    margin-top: 1vh;
    margin-left: 10vw;
    font-size: 12px;
    flex: 50%;
    color: black;
    font-weight: 300;
    width: 30%;
}

.enrollNow {
  margin-top: 20vh;
  margin-bottom: 3vh;
  margin-left: 10vw;
  font-size: medium;
  font-weight: 400;
  border: solid 1px #0077b6;
  padding: 8px 15px;
  color: #0077b6;
  text-decoration: none;
  max-width: fit-content;
}

.layoutBottom{
    margin-top: 6vh;
    margin-left: 10vw;
    display: flex;

}

.layoutLeftBottom {
    display: flex;
    flex-direction: column;
    flex: 40%;
}

.syllabus {
    display: flex;
    flex-direction: column;
    /* border: solid 1px black; */
    border-radius: 10px;
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
}

.syllabusHeader {
    margin: 20px;
    font-size: medium;
}

.descriptionPoints {
    display: flex;
    flex-direction: column;
    
}

.descriptionText {
    margin-left: 6vw;
    font-size: medium;
    font-weight: 300;
    /* padding-top: 20px; */
    padding-bottom: 30px;
}

.skills{
    display: flex;
}

.skill {
  margin-top: 20px;
  margin-left: 10px;
  font-size: medium;
  font-weight: 400;
  border: solid 0px #0077b6;
  padding: 8px 15px;
  color: black;
  max-width: fit-content;
  background-color: rgb(235, 229, 229);
  border-radius: 20px;
}

.layoutRightBottom {
    display: flex;
    flex-direction: column;
    flex: 30%;
}

.instructor {
    display: flex;
    align-items: center;
    margin-left: 3vw;
}

.instructorImg {
    width: 100px;
    height: 100px;
    border-radius: 50px;

}

.instructorInfo {
    display: flex;
    flex-direction: column;
    margin-left: 2vw;

}

.instructorName {
      font-size: 22px;
      padding-bottom: 5px;
      font-weight: 300;
}

.instructorProfession {
    font-size: small;
    font-weight: 300;
    
}

.instructorCompany {
    font-size: small;
    font-weight: 300;
    margin-bottom: 20px;
}

.projectFiles {
    display: flex;
    flex-direction: column;
    margin-top: 2vh;
    margin-bottom: 3vh;
    margin-left: 10vw;
}

.file {
  margin-top: 20px;
  padding: 20px 40px 20px 20px;
  font-size: medium;
  font-weight: 400;
  border: solid 1px black;
  /* padding: 8px 15px; */
  color: black;
  max-width: fit-content;
  background-color: grey;
  /* border-radius: 20px; */
}

.skillWrapper {
    display: flex;
    flex-direction: column;
    /* border: solid 1px black; */
    border-radius: 10px;
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    padding-bottom: 30px;
    margin-top: 20px;
}

.headingProjectLayout {
    margin-top: 10vh;
    margin-left: 10vw;
    font-size: large;
    font-weight: bold;
}

.heProjectLayout {
    margin-top: 5px;
    /* margin-left: 10vw; */
    /* margin-bottom: 10px; */
    /* width: 60%; */
}

.instructorHeading {
    margin: 0 0 5vh 3vw;
    font-size: 32px;
    font-weight: 200;
}

.offeredByWrapper {
    display: flex;
    flex-direction: column;
    text-align: left;
    flex: 40%;
    margin-top: 10vh;
}

.superscript{
    margin: 0;
    padding-left: 9px;
}